.header__link {
  color: #A9A9A9;
  text-decoration: none;
  font-size: 18px;
  line-height: 22px;
  text-align: right;
  margin: 0 30px 39px 0;
}

.header__link:hover {
  cursor: pointer;
  opacity: 0.6;
}

@media screen and (max-width: 660px) {
  .header__link {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 40px;
    color: #A9A9A9;
  }
}