.auth__submit {
  width: 358px;
  height: 50px;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  margin: 216px auto 15px;
  background: #FFF;
  color: #000000;
  padding: 0;
  box-sizing: border-box;
  border-radius: 2px;
}

@media screen and (max-width: 660px) {
  .auth__submit {
    width: 260px;
    height: 46px;
    font-size: 16px;
    line-height: 19px;   
    margin: 173px auto 20px;
  }
}