.header {
  max-width: 880px;
  margin: 0 auto;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  padding-top: 47px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 660px) {
  .header {
    border-top: 1px solid rgba(84, 84, 84, 0.7);
    padding-top: 30px;
    flex-direction: column-reverse;
  }
}